import 'focus-visible';
import React, { createElement } from 'react';
import classNames from 'classnames/bind';
import { CheckboxProps } from 'core/types/checkbox';
import { cssDecorator } from '@front-toolkit/utils';

import styles from './styles.scss';

const cn = classNames.bind(styles);
const cd = cssDecorator(styles);

export const Checkbox: React.FC<CheckboxProps> = ({
  name,
  id,
  size = 'default',
  label,
  disabled = false,
  checked,
  onChange,
  dataTestId,
  fieldName,
  verticalAlign = 'center',
  textLimitWithEllipsis
}) => {
  const checkClassNames = cn(
    'checkbox',
    'checkbox__label',
    `checkbox__label--${ size }`,
    `checkbox--${ verticalAlign }`,
    {
      'checkbox__label--disabled': disabled
    }
  );

  const onChangeEvent = (event: React.ChangeEvent<HTMLInputElement>): void => {
    onChange(event.target.checked, fieldName);
  };

  return (
    <div className={ cn(checkClassNames, styles.checkbox__wrapper) }>
      { createElement(
        `${ label ? 'label' : 'div' }`,
        { className: checkClassNames, role: label ? 'label' : null },
        <>
          <input
            name={ name }
            className={ styles.checkbox__input }
            id={ id }
            type="checkbox"
            disabled={ disabled }
            checked={ checked }
            onChange={ onChangeEvent }
            data-test-id={ dataTestId }
          />
          <span className={ styles.checkbox__icon } />
          { label && (
            <span
              style={ textLimitWithEllipsis ? { maxWidth: textLimitWithEllipsis } : undefined }
              className={ cd('label_text', { limit_with_ellipsis: textLimitWithEllipsis }) }
            >
              { label }
            </span>
          ) }
        </>
      ) }
    </div>
  );
};
